<script>
import _ from 'lodash';
import { mergeValue } from '@/utils/index'
export default {
  data() {
    return {
      activeNames: ['1', '2', '3'],
      male: 1,
      female: 2,
      id: '',
      ruleForm1: {
        name: '',
        birthday: '',
        sex: 1,
        card_id: '',
        phone: '',
        hk_address: '',
        home_address: '',
        unit_address: '',
        income: '',
        spouse_id: '',
        card_type: 1,
        employee_id: '',
        employee_start_time: ''
      },
      ruleForm2: {
        credit_id: '',
        credit_start_time: '',
        credit_expire_time: '',
        credit_adjuster: '',
        credit_level: '',
        customer_manager: '',
        destroy_time: '',
        destroy_reason: ''
      },
      // ruleForm3: {
      //   loans_count: '',
      //   loans_deadline: '',
      //   loans_rate: '',
      //   lending_date: '',
      //   loans_balance: '',
      //   loans_subject: '',
      //   repayment_method: '',
      //   loans_bondsman_ids: '',
      //   primary_person: '',
      //   minor_person: '',
      //   loans_use: '',
      //   loans_kind: '',
      //   loans_type: '',
      //   bondsman_method: '',
      //   loans_status: '',
      //   industry_classify: '',
      //   business_classify: '',
      //   interest_income: '',
      //   overdue_loans: ''
      // },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      cardType: [{
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }, {
        value: 3,
        label: ''
      }]
    };
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    submitForm(formName) {
      const data = this[formName];
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (formName === 'ruleForm1') {
            data.formType = 'base';
          } else if (formName === 'ruleForm2') {
            data.formType = 'credit';
          } else {
            data.formType = 'loans';
          }
          this.$store.dispatch('ModifyPerson', { data, params: { id: this.id } }).then((result) => {
            this.$router.push('/customer/person/list');
          }).catch(function () {
            return new Error('reject again in nested Promise');
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.id = to.params.id;
      vm.$store.dispatch('GetPersonItem', {
        params: {
          id: to.params.id
        }
      }).then(data => {
        vm.ruleForm1 = mergeValue(vm.ruleForm1, data);
        vm.ruleForm2 = mergeValue(vm.ruleForm2, data);
        // vm.ruleForm3 = mergeValue(vm.ruleForm3, data);
      });
    });
  }
}
</script>
<style lang="scss" scoped>
.el-form {
