<template>
  <div class="m-form-person">
    <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <div class="title"></div>
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="birthday">
        <el-date-picker
            v-model="ruleForm.birthday"
            type="date"
            value-format="timestamp"
            placeholder="">
          </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio-group v-model="ruleForm.sex">
          <el-radio :label="male"></el-radio>
          <el-radio :label="female"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="card_id">
        <el-input v-model="ruleForm.card_id"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="hk_address">
        <el-input v-model="ruleForm.hk_address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="home_address">
        <el-input type="textarea" v-model="ruleForm.home_address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="unit_address">
        <el-input type="textarea" v-model="ruleForm.unit_address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="income">
        <el-input v-model="ruleForm.income"></el-input>
      </el-form-item>
      <el-form-item label="" prop="spouse_id">
        <el-input v-model="ruleForm.spouse_id"></el-input>
      </el-form-item>
      <el-form-item label="">
          <el-select v-model="ruleForm.card_type"  placeholder="">
          <el-option
            v-for="item in cardType"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item v-if="ruleForm.type === 2" label="" prop="employee_id">
        <el-input v-model="ruleForm.employee_id"></el-input>
      </el-form-item>
      <el-form-item v-if="ruleForm.type === 2" label="" prop="employee_start_time">
        <el-date-picker
            v-model="ruleForm.employee_start_time"
            type="date"
            value-format="timestamp"
            placeholder="">
          </el-date-picker>
      </el-form-item>
      <div class="form-button">
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      male: 0,
      female: 1,
      ruleForm: {
        name: '',
        birthday: '',
        sex: 0,
        card_id: '',
        phone: '',
        hk_address: '',
        home_address: '',
        unit_address: '',
        income: '',
        spouse_id: '',
        card_type: 1,
        employee_id: '',
        employee_start_time: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      cardType: [{
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }, {
        value: 3,
        label: ''
      }]
    };
  },
  methods: {
    submitForm(formName) {
      const data = this.ruleForm;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch('AddPerson', data).then((result) => {
            this.$router.push('/customer/person/list');
          }).catch(function () {
            return new Error('reject again in nested Promise');
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>
<style lang="scss" scoped>
.el-form {
  width: 880px;
  .title {
    padding-left: 10px;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: bold;
    color: #555;
    border-left: 3px solid #efefef;
    line-height: 20px;
  }
  .form-button {
    padding-top: 20px;
    text-align: center;
  }
}
</style>
<style lang="scss">
.m-form-person {
  .el-form-item__content {
    width: 300px;
  }
  .el-form-item__label {
    font-weight: normal;
  }
}
</style>





